.thank-section {
	position: relative;
	padding: 10rem 0 5rem;
	text-align: center;
	@include max-md {
		padding: 5rem 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 30%;
		left: 0;
		width: 240px;
		transform: translateX(-50%) rotate(15deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 10%;
		right: 0;
		width: 200px;
		transform: translateX(50%) scaleX(-1);
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, 15%);
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 120px;
			transform: translate(-50%, -30%);
			@include max-lg {
				transform: translate(-70%, -30%);
			}
			@include max-md {
				transform: translate(-100%, -40%);
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
			.orn-8 {
				position: absolute;
				top: 0;
				left: 60%;
				width: 40px;
				transform: translate(-50%, -65%) rotate(45deg);
				@include max-md {
					width: 30px;
				}
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100px;
				transform: translate(50%, 10%);
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, 15%) scaleX(-1);
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 120px;
			transform: translate(-50%, -30%);
			@include max-lg {
				transform: translate(-70%, -30%);
			}
			@include max-md {
				transform: translate(-100%, -40%);
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 160px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
			.orn-8 {
				position: absolute;
				top: 0;
				left: 60%;
				width: 40px;
				transform: translate(-50%, -70%) rotate(20deg);
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(60%, 0);
				@include max-md {
					width: 100px;
				}
			}
		}
	}
	.orn-10 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-30%, -10%);
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		.image-element.img-2 {
			opacity: 0.8;
		}
		.orn-11 {
			width: 100px;
			transform: translate(-15%, -30%);
			@include max-md {
				width: 80px;
			}
		}
	}
	.orn-12 {
		position: absolute;
		top: 0;
		right: 0;
		width: 100px;
		transform: translate(30%, -10%) scaleX(-1);
		@include max-md {
			width: 80px;
		}
		.orn-13 {
			width: 100px;
			transform: translateY(-30%);
			@include max-md {
				width: 80px;
			}
			@include max-sm {
				width: 80px;
			}
		}
	}
	.orn-14 {
		position: absolute;
		top: 3rem;
		left: -25%;
		width: 180px;
		animation: cloudMove 25000ms linear infinite 3000ms;
	}
	.orn-15 {
		position: absolute;
		top: 8rem;
		left: -25%;
		width: 160px;
		animation: cloudMove 22000ms linear infinite;
	}
	.orn-16 {
		position: absolute;
		top: 50%;
		left: -25%;
		width: 140px;
		animation: cloudMove 21000ms linear infinite 5000ms;
	}
	.image-wrapper {
		position: relative;
		width: 80%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		border-radius: 500px 500px 10px 10px;
		background-color: var(--color-main);
		overflow: hidden;
		@include max-lg {
			width: 100%;
		}
		.elements-absolute {
			top: unset;
			bottom: 0;
			left: 0;
			width: 100%;
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: var(--border-radius);
		}
	}
	.thank-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		@include max-md {
			.row {
				gap: 2rem;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
