.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, 10%);
		@include max-lg {
			width: 120px;
			transform: translate(-30%, 0);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		.image-element.img-2 {
			opacity: 0.5;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 200px;
			transform: translate(-15%, -30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 10%) scaleX(-1);
		@include max-lg {
			width: 120px;
			transform: translate(30%, 0%) scaleX(-1);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 180px;
			transform: translateY(-30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 35%;
		left: 0;
		width: 240px;
		transform: translateX(-50%) scaleX(-1) rotate(-45deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 60%;
		left: 0;
		width: 200px;
		transform: translateX(-50%);
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		top: 30%;
		right: 0;
		width: 240px;
		transform: translateX(50%) rotate(-35deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 220px;
		transform: translateX(50%) rotate(-35deg);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(30%, 0%) scaleX(-1) rotate(-30deg);
			@include max-md {
				width: 160px;
				transform: translate(60%, 0) scaleX(-1) rotate(-30deg);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		padding: 2rem;
		z-index: 5;
		@include max-md {
			padding: 2rem 1rem;
		}
		.stream-content {
			position: relative;
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: 500px 500px 0 0;
				background-color: var(--color-light);
				content: "";
				opacity: 0.6;
			}
			.stream-description {
				position: relative;
				padding: 8rem 2rem 3rem;
				@include max-md {
					padding: 5rem 1rem 2rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}

[data-anim="flamingo-right-2"].has-animate {
	animation-name: flamingo-right-2;
}

@keyframes flamingo-right-2 {
	0% {
		transform: translate3d(-50%, 5%, 0);
	}
	20% {
		transform: translate3d(-40%, 0, 0);
	}
	40% {
		transform: translate3d(-30%, 5%, 0);
	}
	60% {
		transform: translate3d(-20%, 0, 0);
	}
	80% {
		transform: translate3d(-10%, 5%, 0);
	}
	100% {
		transform: translate3d(0%, 0, 0);
	}
}
