.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, 10%);
		@include max-lg {
			width: 120px;
			transform: translate(-30%, 0);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		.image-element.img-2 {
			opacity: 0.5;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 200px;
			transform: translate(-15%, -30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 10%) scaleX(-1);
		@include max-lg {
			width: 120px;
			transform: translate(30%, 0%) scaleX(-1);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 180px;
			transform: translateY(-30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 2%;
		left: 0;
		width: 240px;
		transform: translate(-50%, 0) rotate(35deg) scaleX(-1);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			bottom: 0;
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 60%;
		right: 0;
		width: 240px;
		transform: translate(50%, -50%) rotate(-35deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			top: 80%;
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, 40%);
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-100%, 0);
			@include max-xs {
				width: 120px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, 40%) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-120%, 0);
			@include max-xs {
				width: 120px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 4/6;
		padding-top: 1rem;
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 500px 500px 0 0;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 0;
		background: linear-gradient(180deg, var(--color-primary), transparent);
		border-bottom-right-radius: var(--border-radius);
		border-bottom-left-radius: var(--border-radius);
		box-shadow: 0 10px 1rem rgba($color: #000000, $alpha: 0.05);
		@include max-lg {
			padding: 1rem 0;
		}
		.title-section {
			&::before {
				all: unset;
			}
			h2 {
				color: var(--color-dark);
			}
		}
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
