.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 5rem;
		left: 0;
		width: 220px;
		transform: translateX(-50%);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			top: 10rem;
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 220px;
		transform: translateX(50%) scaleX(-1);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			top: 8rem;
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translate(-50%, -50%) rotate(15deg);
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 200px;
		transform: translate(50%, -50%) rotate(-15deg) scaleX(-1);
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-65%, 50%);
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				width: fit-content;
				max-width: 100%;
				margin: 0 auto;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				border: 4px solid var(--color-secondary);
				background-color: transparent;
				height: 100%;
				border-radius: 500px 500px 10px 10px;
				text-align: center;
				overflow: hidden;
				&::before {
					position: absolute;
					inset: 0;
					top: 1rem;
					background-color: var(--color-main);
					opacity: 0.6;
					border-radius: 500px 500px 0 0;
					content: "";
					z-index: -1;
				}
				.elements-absolute {
					top: unset;
					height: 110%;
					.background-element {
						img {
							height: auto;
							object-position: top;
						}
					}
				}
				.card-body {
					padding: 3rem 2rem;
					@include max-lg {
						padding: 2rem 1rem;
					}
					h4 {
						font-weight: 600;
						margin: 1rem 0;
						@include max-md {
							font-size: 24px;
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 2rem;
		border-radius: 10px;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue-sm {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			width: 100%;
			text-align: center;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			div {
				position: relative;
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
