.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.scroll-icon {
		position: relative !important;
		z-index: 10;
	}
	.orn-1 {
		position: absolute;
		top: 4rem;
		left: -25%;
		width: 180px;
		animation: cloudMove 25000ms linear infinite 3000ms;
	}
	.orn-2 {
		position: absolute;
		top: 10rem;
		left: -25%;
		width: 160px;
		animation: cloudMove 22000ms linear infinite;
		@include max-lg {
			top: 20%;
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: -25%;
		width: 140px;
		animation: cloudMove 21000ms linear infinite 5000ms;
		@include max-lg {
			top: 40%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 25%;
		width: 200px;
		transform: translate(-50%, 15%);
		@include max-xl {
			left: 15%;
		}
		@include max-lg {
			left: 0;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-50%, -30%);
			@include max-lg {
				transform: translate(-70%, -30%);
			}
			@include max-md {
				transform: translate(-100%, -40%);
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
			.orn-8 {
				position: absolute;
				top: 0;
				left: 60%;
				width: 60px;
				transform: translate(-50%, -65%) rotate(45deg);
				@include max-md {
					width: 50px;
				}
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(50%, 10%);
				@include max-md {
					width: 120px;
				}
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 25%;
		width: 200px;
		transform: translate(50%, 15%) scaleX(-1);
		@include max-xl {
			right: 15%;
		}
		@include max-lg {
			right: 0;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-50%, -30%);
			@include max-lg {
				transform: translate(-70%, -30%);
			}
			@include max-md {
				transform: translate(-100%, -40%);
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
			.orn-8 {
				position: absolute;
				top: 0;
				left: 60%;
				width: 50px;
				transform: translate(-50%, -70%) rotate(20deg);
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 140px;
				transform: translate(60%, 0);
				@include max-md {
					width: 120px;
				}
			}
		}
	}
	.orn-10 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, -10%);
		@include max-md {
			width: 130px;
		}
		@include max-sm {
			width: 100px;
		}
		.image-element.img-2 {
			opacity: 0.8;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-11 {
			width: 200px;
			transform: translate(-15%, -30%);
			@include max-md {
				width: 160px;
			}
			@include max-sm {
				width: 140px;
			}
		}
	}
	.orn-12 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, -10%) scaleX(-1);
		@include max-md {
			width: 130px;
		}
		@include max-sm {
			width: 100px;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-13 {
			width: 180px;
			transform: translateY(-30%);
			@include max-md {
				width: 140px;
			}
			@include max-sm {
				width: 120px;
			}
		}
	}
	.elements-absolute {
		position: absolute;
		top: unset;
		right: unset;
		bottom: 0;
		left: 50%;
		width: 50%;
		height: auto;
		transform: translateX(-50%);
		@include max-xl {
			width: 70%;
		}
		@include max-lg {
			width: 120%;
		}
		@include max-md {
			width: 150%;
		}
		.background-element {
			display: block;
			height: auto;
			.background-wrapper {
				width: 100%;
				height: auto;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 6/4;
		margin: 0 auto;
		margin-top: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 8px;
			.background-header {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		.header {
			position: relative;
			display: block;
			width: 50%;
			padding: 4rem 0;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
				padding: 4rem 0;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					padding: 3rem 0;
					border-radius: var(--border-radius);
					@include max-md {
						padding: 2rem 1rem;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
