.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		transform: translateX(-50%);
		width: 200px;
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 240px;
		transform: translate(40%, -50%) scaleX(-1) rotate(-45deg);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 8rem 2rem 3rem;
		background: var(--color-main);
		border-radius: 500px 500px 30px 30px;
		@include max-md {
			padding: 5rem 1rem 2rem;
		}
		.background-element {
			img {
				object-fit: cover;
				object-position: 50% 100%;
				border-radius: 500px 500px 30px 30px;
			}
		}
	}
}
