.wishes-section {
	position: relative;
	padding: 5rem 0;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		width: 200px;
		top: 10%;
		left: 0;
		transform: translate(-50%, 0);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			top: 0;
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translate(-50%, 15%);
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(-50%, -30%);
			@include max-lg {
				transform: translate(-70%, -30%);
			}
			@include max-md {
				width: 160px;
				transform: translate(-100%, -40%);
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 220px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 180px;
			}
			@include max-xs {
				width: 160px;
			}
			.orn-8 {
				position: absolute;
				top: 0;
				left: 60%;
				width: 60px;
				transform: translate(-50%, -65%) rotate(45deg);
				@include max-md {
					width: 50px;
				}
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 180px;
				transform: translate(50%, 10%);
				@include max-md {
					width: 140px;
				}
				@include max-xs {
					width: 120px;
				}
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(50%, 15%) scaleX(-1);
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(-50%, -30%);
			@include max-lg {
				transform: translate(-70%, -30%);
			}
			@include max-md {
				width: 160px;
				transform: translate(-100%, -40%);
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 220px;
			transform: translate(40%, 20%);
			@include max-md {
				width: 180px;
			}
			@include max-xs {
				width: 160px;
			}
			.orn-8 {
				position: absolute;
				top: 0;
				left: 60%;
				width: 50px;
				transform: translate(-50%, -70%) rotate(20deg);
				@include max-md {
					width: 40px;
				}
			}
			.orn-9 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 220px;
				transform: translate(60%, 20%);
				@include max-md {
					width: 160px;
				}
				@include max-xs {
					width: 140px;
				}
			}
		}
	}
	.elements-absolute {
		.background-element {
			img {
				object-position: 50% 100%;
			}
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		border-radius: 500px 500px 30px 30px;
		position: relative;
		z-index: 3;
		overflow: hidden;
		&.card {
			border: none;
		}
		.card-body {
			padding: 8rem 2rem 3rem;
			@include max-md {
				padding: 5rem 1rem 2rem;
			}
			.form-control {
				background-color: rgba($color: #fff, $alpha: 0.8);
			}
		}
	}

	.wishes-preview {
		position: relative;
		@include max-lg {
			margin-top: 2rem;
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					height: 28rem;
					overflow: auto;
					padding-right: 1rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.8);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.8);
							}
						}
					}
				}
			}
		}
	}
}
