.story-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, 10%);
		@include max-lg {
			width: 120px;
			transform: translate(-30%, 0);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 100px;
		}
		.image-element.img-2 {
			opacity: 0.5;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 200px;
			transform: translate(-15%, -30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
				transform: translate(-15%, -40%);
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 10%) scaleX(-1);
		@include max-lg {
			width: 120px;
			transform: translate(30%, 0%) scaleX(-1);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 100px;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 180px;
			transform: translateY(-30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
				transform: translateY(-40%);
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 1rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		height: auto;
		@include max-md {
			aspect-ratio: 4/3;
		}
		.story-background {
			position: relative;
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
