.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, 10%);
		@include max-lg {
			width: 120px;
			transform: translate(-30%, 0);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		.image-element.img-2 {
			opacity: 0.5;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 200px;
			transform: translate(-15%, -30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
				transform: translate(-15%, -40%);
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 10%) scaleX(-1);
		@include max-lg {
			width: 120px;
			transform: translate(30%, 0%) scaleX(-1);
		}
		@include max-md {
			width: 100px;
		}
		@include max-sm {
			width: 80px;
		}
		img {
			transform-origin: 10% 0%;
		}
		.orn-3 {
			width: 180px;
			transform: translateY(-30%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
				transform: translateY(-40%);
			}
			@include max-sm {
				width: 100px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 220px;
		transform: translate(-60%, -30%) rotate(25deg);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			top: 30%;
			width: 160px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-5 {
			width: 240px;
			transform: translate(20%, -30%) rotate(-25deg);
			@include max-lg {
				width: 220px;
			}
			@include max-md {
				width: 160px;
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 220px;
		transform: translate(-50%, 20%) rotate(20deg);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-7 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 240px;
		transform: translate(40%, 0) scaleX(-1);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			top: 60%;
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(50%, 20%) scaleX(-1);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-9 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 240px;
		transform: translate(30%, -50%) scaleX(-1) rotate(-45deg);
		@include max-md {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-10 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translateX(-10%) scaleX(-1) rotate(20deg);
		img {
			transform-origin: 50% 100%;
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		z-index: 3;
		@include max-md {
			margin-bottom: 1rem;
		}
		span {
			position: relative;
			font-size: 28px;
			font-family: "Le Major", cursive;
			@include max-md {
				font-size: 24px;
			}
		}
	}
	.couple-content {
		position: relative;
		margin-bottom: 2rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 4/6;
			height: auto;
			margin: 0 auto;
			border-radius: 1000px 1000px 0 0;
			padding-top: 2rem;
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 1000px 1000px 0 0;
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
			h3 {
				color: var(--color-secondary);
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		padding: 3rem 0 5rem;
		@include max-md {
			padding: 3rem 0;
		}
		.elements-wrapper-absolute {
			height: calc(100% + 5rem);
		}
	}
}
